@import "variable.scss";

.footer {
  background-color: $background-color;
  display: flex;
  justify-content: space-between;
  background-color: rgb(202, 199, 195, 0.4);
  text-align: center;
  padding-bottom: 10px;

  .footer-content {
    text-align: center;
    margin: auto;

    .logo {
      font-size: 1.2rem;
      margin-top:10px;
      .logo-g {
        font-size: 1.7rem;
      }
    }

    .footer-list {
      display: flex;
      justify-content: space-between;
      margin: 10px 0 0px 20px;

      text-align: center;

      li {
        margin: 0 15px 0 15px;
        font-size: 0.7rem;

        a {
          color: $main-letter-color;
        }
        & a:hover {
          color: rgb(24, 23, 23);
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .footer-list li a {
    font-size: 1rem;
  }

  .logo{
    font-size:0.8rem;
  }
}
