@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Tsukushi A Round Gothic";
}

body {
  width: 100%;
}

.wrap {
  width: 100%;
  max-width: 1200px;
  min-height: 77vh;
  margin: 30px auto;
}

/* タイムライン・イベント詳細ページ　共通 */
/* a */
/* 　　　　　　2カラムの時に使える【　枠　】テンプレートと背景
　　　　　　　　　　　　timelineとrecommendationの共通設定から拝借 */
/* ２カラム両方の【　枠　】設定 */
/*  ↓↓↓↓↓↓使用例  それぞれのコンテンツに指定する
 .side {
    @include wrap();

  }
  .main-content {
    @include wrap();

  }
*/
/* 背景*/
.bg-img {
  background-color: rgba(202, 199, 195, 0.4);
  background-size: cover;
  background-repeat: repeat-y;
  width: 100%;
  height: 100％;
}

/* sideとmain-contentのモバイルのレスポンシブの共通設定　モバイルファースト */
/*
.contents-wrapper {　　←２カラム入ってる親に指定することで、２カラムがレスポンシブされる
    @include contents-wrapper();
}

/* wrap PC　version 　　コピペで貼り付けOK*/
@media (min-width: 769px) {
  .contents-wrapper {
    display: flex;
    flex-direction: row;
  }
  .contents-wrapper .side {
    width: 30%;
    min-width: 300px;
  }
  .contents-wrapper .mian-content {
    width: 80%;
    min-width: 500px;
  }
}
/* icon */
/* メインパーツにid or class をindexをボディにつけ、その中に入れ子にしてCSSを記述する */
/* プラウザのキャッシュクリア　ショートカット　Command ＋shift + R */
/* フォントファミリー　あとで決める */
/* Scss記述に修正する。 */
/* ログインしたときの色 */
/* ログインしている時 いろ変えたい時 */
/* .header-color{
  background-color:transparent;
}  */
/* ログイン前のヘッダー */
#index {
  position: relative;
}

.main_header {
  background-color: rgba(202, 199, 195, 0.4);
  position: relative;
  /* navBtn用のposition:relative */
}
.main_header .header-contents {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 5px 10px 0;
  /* .logo {
    align-items: top;
  } */
}
.main_header .header-contents a {
  font-size: 1.5rem;
}
.main_header .header-contents a:hover {
  color: black;
  cursor: pointer;
}
.main_header .header-contents .header-list {
  display: flex;
}
.main_header .header-contents .header-list li:not(:last-of-type) {
  padding-right: 40px;
}
.main_header .header-contents .header-list li {
  text-align: center;
}
.main_header .header-contents .header-list li a {
  font-size: 0.9rem;
  font-weight: bold;
}
.main_header .header-contents .header-list li a span {
  font-size: 0.7vw;
}
.main_header .header-contents .header-list li a:hover {
  color: black;
  cursor: pointer;
}

.header-list2 {
  display: flex;
  text-align: right;
  font-size: 0.5rem;
  padding-top: 10px;
  /*   li:not(:first-of-type) {
    padding-right: 40px;

  } */
}
.header-list2 li {
  /*   padding-left: 40px; */
  text-align: right;
}
.header-list2 li .list {
  font-size: 1rem;
  font-weight: bold;
}
.header-list2 li:hover {
  color: black;
  cursor: pointer;
}
.header-list2 li:not(:first-of-type) {
  margin-left: 40px;
}

/* ログイン後 */
.account-name {
  color: #635a5a;
  font-weight: bold;
  font-size: 1rem;
  padding-top: 10px;
  /* margin-left: 50px; */
}

/* navigation menu ハンバーガー */
@media (max-width: 768px) {
  .header-contents {
    height: 50px;
  }
  .header-contents .logo {
    margin-top: -8px;
  }
  .header-contents .logo a {
    font-size: 1.1rem;
  }

  .account-name {
    margin-right: 90px;
  }

  /* ハンバーガーメニュー */
  .header-list2 {
    display: none;
    opacity: 0;
    z-index: 100;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 6vh;
    background-color: #eae9e7;
    width: 100%;
    transition: ease 0.3s;
    position: fixed;
  }
  .header-list2 li {
    margin-top: 3px;
    /*    text-align: center; */
  }
  .header-list2 li:last-child {
    margin-right: 38px;
  }
  .header-list2 .header-list2.active {
    opacity: 1;
  }

  /* クリック前のボタン */
  .navBtn {
    z-index: 300;
    display: block;
    transition: 0.2s;
    top: 0;
    right: 0;
  }

  .navBtn div {
    position: absolute;
    z-index: 300;
    top: 22px;
    right: 15px;
    background-color: #635a5a;
    height: 1.5px;
    width: 27px;
    transition: 0.2s;
  }
  .navBtn div:nth-of-type(1) {
    transform: translate(0%, -10px);
  }
  .navBtn div:nth-of-type(3) {
    transform: translate(0%, 10px);
  }

  .navBtn.active div:nth-of-type(2) {
    opacity: 0;
  }

  .navBtn.active div:nth-of-type(1) {
    transform: rotate(45deg);
  }

  .navBtn.active div:nth-of-type(3) {
    transform: rotate(-45deg);
  }

  .navBtn.active {
    position: fixed;
  }
}
