/* -----------------------TOP VISUAL-------------------- */
/* 画像スライドショーにする ---ok*/
.top-visual {
  width: 100vw;
  height: 60vh;
  transition: background-image 3s ease;
  background-image: url(../images/sunset-4937813_1920.jpg);
  background-color: rgba(202, 199, 195, 0.4);
  background-position: center center;
  background-size: cover;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: white;
}
.top-visual .msg-1,
.top-visual .msg-2 {
  margin-left: 40px;
  text-shadow: 0 0 40px #131312;
  transition: all 1.5s ease;
  z-index: 10;
}
.top-visual .msg-1.hide,
.top-visual .msg-2.hide {
  opacity: 0;
}
.top-visual .msg-1 {
  left: 9%;
  top: 20%;
  position: absolute;
}
.top-visual .msg-2 {
  top: 60%;
  right: 10%;
  position: absolute;
}

@media (min-width: 768px) {
  .top-visual {
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: relative;
  }
  .top-visual .msg-1,
.top-visual .msg-2 {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .top-visual .msg-1,
.top-visual .msg-2 {
    font-size: 1.8rem;
  }
  .top-visual .msg-1 {
    padding-left: 150px;
  }
  .top-visual .msg-2 {
    padding-right: 200px;
  }
}
/* hidden-message */
#hidden-message {
  transition: all 1s ease;
  display: none;
  opacity: 0;
  background-color: rgba(66, 65, 65, 0.6);
  text-align: center;
  width: 100vw;
  height: 60vh;
  position: relative;
  color: white;
  font-size: 0.75rem;
  padding: 15px;
}
#hidden-message.show {
  animation-name: fadeIn;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  /* toで止める */
}
#hidden-message .message-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    /*  transform: translateY(-500px); */
  }
  to {
    opacity: 1;
    transform: none;
  }
}
/* hidden-message 320px version */
@media (max-width: 320px) {
  #hidden-message {
    font-size: 0.65rem;
  }
}
/* hidden-message PC version*/
@media (min-width: 768px) {
  #hidden-message {
    padding-top: 70px;
    font-size: 1.1rem;
  }
}
@media (min-width: 1000px) {
  #hidden-message {
    padding-top: 100px;
    line-height: 1.6;
  }
}
/* --------------------MAIN-CONTENTS---------------------- */
/* main-contentsの全体共通 */
.space {
  opacity: 0;
}

main {
  background-color: rgba(202, 199, 195, 0.4);
  font-family: "Tsukushi A Round Gothic";
  color: #635a5a;
}

/* section の枠*/
.section-wrapper {
  max-width: 850px;
  width: 90%;
  margin: 0 auto;
  background-color: white;
  font-family: "Tsukushi A Round Gothic";
  padding-bottom: 30px;
}

/* section title  */
.section-title {
  font-size: 0.9rem;
  color: #635a5a;
  text-align: center;
  margin-bottom: 10px;
  padding: 20px;
  transition: all 1s ease;
}
.section-title p {
  font-size: 0.7rem;
}

/* ---------------------ABOUT--------------------- */
.about-contents {
  width: 75%;
  margin: 0 auto;
}
.about-contents .first-image {
  text-align: center;
}
.about-contents .secound-image {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.about-contents .secound-image .about-list {
  text-align: center;
  text-align: left;
}
.about-contents .secound-image .about-list li:first-child {
  margin-top: 20px;
}
.about-contents .secound-image .about-list li:not(:first-of-type) {
  margin-top: 10px;
}
.about-contents .secound-image img {
  margin: 0 15px;
}

