/* 各ステップとリボン */
.how-to {
  font-size: 0.9rem;
  width: 50%;
  padding: 0 20px;
  margin-right: 5px;
  text-align: center;
}
.how-to .step-ribbon {
  margin-left: 5px;
  border-bottom: black;
  position: relative;
  text-align: center;
  background: #19eeb9;
  width: 100px;
  height: 30px;
  font-family: "Tsukushi A Round Gothic";
  color: #635a5a;
}
.how-to .step-ribbon::before {
  position: absolute;
  top: -7px;
  left: -7px;
  width: 100%;
  height: 30px;
  content: "";
  border: 1px solid #000;
