@import "variable.scss";
/* ------------------タイムライン,サーチ中身の設定---------------------- */



/* ボタン */
@mixin button {
  color: $button-color;
  background-color: $button-color;
      padding: 6px 15px;
      border-radius: 2px;
      border: none;
      color: $main-letter-color;
      &:hover {
        color: black;
        box-shadow:1px 1px rgb(12, 128, 12) ;
      }
}

/* a */
@mixin a{
  color: $main-letter-color;

        &:hover{
          color: $button-color
        }
}
a{
  color: $main-letter-color;

&:hover{
  color: $button-color
}

}



/*----------------------- .seach */



  .search {
    @include timeline-wrap();


  text-align: center;

  .aside-title {
    padding-top:20px;
    margin: auto;
    width: 70%;
    color: $main-letter-color;
    font-size: 1rem;/* -------------フォントサイズ　仮------------- */
  }

  form {
    padding: 10px 0 15px;

    dd input[type="search"],
    [type="date"] {
      width: 70%;
      padding: 5px 8px;
      margin: 5px;
    }
    dd input[type="submit"] {
     @include button();
    }
  }

  .source-quote {
    @include quote();
  }
}
